import request from '@/utils/request'
// 获取精品歌单
export function apiGetPlaylistHq(limit, cat) {
    return request({
        url: '/top/playlist/highquality',
        method: 'get',
        params: {
            cat,
            limit
        }
    })
}

// 获取歌单列表
export function apiGetPlaylist(limit, cat, offset) {
    return request({
        url: '/top/playlist',
        method: 'get',
        params: {
            cat,
            limit,
            offset
        }
    })
}

// 获取歌单详情
export function apiGetPlaylistDetail(id) {
    return request({
        url: '/playlist/detail',
        method: 'get',
        params: {
            id
        }
    })
}