// 通用的请求方法
import request from '@/utils/request'
// 通过ID 获取URL
// /song/url?id=33894312
export async function apiGetSongUrl(id) {
    let { data } = await request({
        url: '/song/url',
        method: 'get',
        params: {
            id
        }
    })
    let url = data.data[0].url
    return url
}
