<!-- 最新音乐 -->
<template>
  <div class="songs-container">
    <div class="tab-bar">
      <span class="item" @click="tag = 0" :class="{ active: tag == 0 }">
        全部
      </span>
      <span class="item" @click="tag = 7" :class="{ active: tag == 7 }">
        华语
      </span>
      <span class="item" @click="tag = 96" :class="{ active: tag == 96 }">
        欧美
      </span>
      <span class="item" @click="tag = 8" :class="{ active: tag == 8 }">
        日本
      </span>
      <span class="item" @click="tag = 16" :class="{ active: tag == 16 }">
        韩国
      </span>
    </div>
    <!-- 内容tabel -->
    <table class="el-table playlit-table">
      <thead>
        <th></th>
        <th></th>
        <th>音乐标题</th>
        <th>歌手</th>
        <th>专辑</th>
        <th width="80px">时长</th>
      </thead>
      <tbody>
        <tr v-for="(item, index) in lists" :key="index" class="el-table__row">
          <td>{{ index + 1 }}</td>
          <td>
            <div class="img-wrap" @click="playMusic(item.id)">
              <img :src="item.album.picUrl" alt="" />
              <!-- 播放按钮 -->
              <span class="iconfont icon-play"></span>
            </div>
          </td>
          <td>
            <div class="song-wrap">
              <div class="name-wrap">
                <span>{{ item.name }}</span>
                <span class="iconfont icon-mv"></span>
              </div>
              <span></span>
            </div>
          </td>
          <td>{{ item.album.artists["0"].name }}</td>
          <td>{{ item.album.name }}</td>
          <td>{{ item.duration | formatDuration }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { apiGetTopSongs } from "@/api/topSongs.js";
import { apiGetSongUrl } from "@/api/index.js";
export default {
  name: "songs",
  data() {
    return {
      // 歌曲列表
      lists: [],
      // 分类
      tag: 0,
    };
  },
  watch: {
    tag() {
      this.getList();
      console.log(this.tag);
    },
  },
  created() {
    this.getList();
  },
  methods: {
    // 获取 最新音乐数据
    async getList() {
      let { data } = await apiGetTopSongs(this.tag);
      console.log(data.data);
      this.lists = data.data.slice(0, 10);
      console.log(this.lists);
    },
    async playMusic(id) {
      console.log(id);
      // let data = await apiGetSongUrl(1958624724);
      // console.log(data);
      // this.$store.dispatch("setMusicUrl", url);
    },
  },
  filters: {
    // 时长过滤器 将毫秒换算成分钟与秒
    formatDuration(value) {
      let minute = Math.floor(value / 1000 / 60);
      let second = Math.floor((value / 1000) % 60);
      return `${minute}:${second < 10 ? "0" + second : second}`;
    },
  },
};
</script>

<style></style>
