<template>
  <div class="discovery-container">
    <!-- 轮播图 -->
    <el-carousel :interval="4000" type="card">
      <el-carousel-item v-for="(item, index) in banners" :key="index">
        <img :src="item.imageUrl" />
      </el-carousel-item>
    </el-carousel>
    <!-- 推荐歌单 -->
    <div class="recommend">
      <h3 class="title">推荐歌单</h3>
      <div class="items">
        <div class="item" v-for="(item, index) in personList" :key="index">
          <div class="img-wrap">
            <div class="desc-wrap">
              <span class="desc">播放量：{{ item.playCount | formatCount }}次</span>
            </div>
            <img :src="item.picUrl" />
            <span class="iconfont icon-play" @click="toPlaylist(item.id)"></span>
          </div>
          <p class="name">{{ item.name }}</p>
        </div>
      </div>
    </div>
    <!-- 最新音乐 -->
    <div class="news">
      <h3 class="title">最新音乐</h3>
      <div class="items">
        <div class="item" v-for="(item, index) in newsongs" :key="index">
          <div class="img-wrap" @click="playMusic(item.id)">
            <!-- 封面 -->
            <img :src="item.picUrl" alt="" />
            <span class="iconfont icon-play"></span>
          </div>
          <div class="song-wrap">
            <!-- 歌名 -->
            <div class="song-name">{{ item.name }}</div>
            <!-- 歌手名 -->
            <div class="singer">{{ item.song.artists[0].name }}</div>
          </div>
        </div>
      </div>
    </div>
    <!-- 推荐MV -->
    <div class="mvs">
      <h3 class="title">推荐MV</h3>
      <div class="items">
        <div class="item" v-for="(item, index) in mvs" :key="index">
          <div class="img-wrap">
            <img :src="item.picUrl" alt="" />
            <span class="iconfont icon-play"></span>
            <div class="num-wrap">
              <div class="iconfont icon-play"></div>
              <!-- 播放次数 -->
              <div class="num">播放:{{ item.playCount }}次</div>
            </div>
          </div>
          <div class="info-wrap">
            <!-- mv名 -->
            <div class="name">{{ item.name }}</div>
            <!-- 歌手名 -->
            <div class="singer">{{ item.artistName }}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// 导入 axios
import { apiGetBannerList, apiGetPersonList, apiGetNewSong, apiGetPersonMv } from "@/api/discovery.js";
// 通用的请求方法
import { apiGetSongUrl } from "@/api/index.js";
export default {
  name: "discovery",
  data() {
    return {
      banners: [],
      personList: [],
      newsongs: [],
      mvs: [],
    };
  },
  methods: {
    // 获取轮播图
    async getBannerList() {
      let { data } = await apiGetBannerList();
      // console.log(data);
      this.banners = data.banners;
    },
    // 获取推荐歌单
    async getPersonList() {
      let { data } = await apiGetPersonList();
      let { result } = data;
      // console.log(result);
      this.personList = result;
    },
    // 获取最新音乐
    async getNewSong() {
      let { data } = await apiGetNewSong();
      // console.log(data.result);
      this.newsongs = data.result;
    },
    // 播放音乐
    async playMusic(id) {
      // 获取音乐地址
      let res = await apiGetSongUrl(id);
      this.$store.dispatch("setMusicUrl", res);
    },
    // 推荐MV
    async getPersonMv() {
      let { data } = await apiGetPersonMv();
      console.log(data.result);
      this.mvs = data.result;
    },
    // 跳转到歌单详情页
    toPlaylist(id) {
      this.$router.push({
        path: "/playlist",
        query: { id },
      });
    },
  },
  created() {
    this.getBannerList();
    this.getPersonList();
    this.getNewSong();
    this.getPersonMv();
  },
  // 播放次数转化为万
  filters: {
    formatCount(value) {
      return value > 10000 ? (value / 10000).toFixed(1) + "万" : value;
    },
  },
};
</script>

<style scoped>
.img-wrap img {
  object-fit: cover;
  height: 140px;
}
</style>
