  <template>
  <div class="mvs-container">
    <div class="filter-wrap">
      <div class="seciton-wrap">
        <!-- 分类切换 地区 -->
        <span class="section-type">地区:</span>
        <ul class="tabs-wrap">
          <li class="tab" v-for="(item, index) in tabArea" :key="index">
            <span class="title" :class="{ active: area == item }" @click="area = item"> {{ item }} </span>
          </li>
        </ul>
      </div>
      <div class="type-wrap">
        <span class="type-type">类型:</span>
        <ul class="tabs-wrap">
          <li class="tab" v-for="(item, index) in tabType" :key="index">
            <span class="title" :class="{ active: type == item }" @click="type = item"> {{ item }} </span>
          </li>
        </ul>
      </div>
      <div class="order-wrap">
        <span class="order-type">排序:</span>
        <ul class="tabs-wrap">
          <li class="tab" v-for="(item, index) in tabOrder" :key="index">
            <span class="title" :class="{ active: order == item }" @click="order = item"> {{ item }} </span>
          </li>
        </ul>
      </div>
    </div>
    <!-- mv容器 -->
    <!-- 推荐MV -->
    <div class="mvs">
      <div class="items">
        <div class="item" v-for="(item, index) in list" :key="index">
          <div class="img-wrap">
            <img :src="item.cover" alt="" />
            <div class="num-wrap">
              <div class="iconfont icon-play"></div>
              <div class="num">{{ item.playCount }}</div>
            </div>
          </div>
          <div class="info-wrap">
            <div class="name">{{ item.name }}</div>
            <div class="singer">{{ item.artistName }}</div>
          </div>
        </div>
      </div>
      <!-- 分页器 -->
      <el-pagination
        @current-change="handleCurrentChange"
        background
        layout="prev, pager, next"
        :total="total"
        :current-page="page"
        :page-size="5"
        :limit="limit"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import { apiGetMvList } from "@/api/mvs";
export default {
  name: "mvs",
  data() {
    return {
      // 地区 类型 排序tab栏死数据
      tabArea: ["全部", "内地", "港台", "欧美", "日本", "韩国"],
      tabType: ["全部", "官方版", "原声", "现场版", "网易出品"],
      tabOrder: ["上升最快", "最热", "最新"],
      // 总条数
      total: 20,
      // 页码
      page: 1,
      // 页容量
      limit: 8,
      // 地区
      area: "全部",
      // 类型
      type: "全部",
      // 排序
      order: "上升最快",
      // 查询的结果
      list: [],
    };
  },
  created() {
    // 获取数据
    this.getList();
  },
  methods: {
    // 获取MV列表
    async getList() {
      let { data } = await apiGetMvList(this.area, this.type, this.order, this.limit, this.offset);
      this.list = data.data;
    },
    handleCurrentChange(val) {
      // console.log(`当前页: ${val}`)
      // 保存页面 重新获取数据
      this.page = val;
      this.getList();
    },
  },
  watch: {
    area(val) {
      // console.log(val);
      this.getList();
    },
    type(val) {
      // console.log(val);
      this.getList();
    },
    order(val) {
      // console.log(val);
      this.getList();
    },
  },
  computed: {
    offset() {
      return (this.page - 1) * this.limit;
    },
  },
};
</script>

<style></style>
