<template>
  <div class="top-container">
    <div class="left-box">
      <!-- <div class="icon-wrapper">
        <i class="iconfont icon-home"></i>
        <i class="iconfont icon-sami-select"></i>
        <i class="iconfont icon-full-screen"></i>
        <h3>小裴播放器</h3>
      </div>
      <div class="history-wrapper">
        <span class="iconfont icon-arrow-lift"></span>
        <span class="iconfont icon-arrow-right"></span>
      </div> -->
    </div>
    <div class="right-box">
      <div class="el-input el-input--small el-input--prefix">
        <input
          type="text"
          autocomplete="off"
          placeholder="搜索"
          class="el-input__inner"
          @keyup.enter="search"
          v-model="keywords"
        />
        <span class="el-input__prefix">
          <i class="el-input__icon el-icon-search"></i>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "top",
  data() {
    return {
      keywords: "",
    };
  },
  methods: {
    search() {
      if (!this.keywords.trim()) return this.$message.error("请输入关键字");
      if (this.$route.path === "/result") this.$router.replace({ path: "/result", query: { keywords: this.keywords } });
      else this.$router.push({ path: "/result", query: { keywords: this.keywords } });
    },
  },
};
</script>

<style scoped></style>
