import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
        // songs id
        musicUrl: "",
        mvUrl: ""
    },
    actions: {
        setMusicUrl({ commit }, url) {
            {
                commit('SETMUSICURL', url)
            }
        },
        setMvUrl({ commit }, url) {
            {
                commit('SETMVURL', url)
            }
        }

    },
    mutations: {
        SETMUSICURL(state, url) {
            state.musicUrl = url
        },
        SETMVURL(state, url) {
            state.mvUrl = url
        }
    },

})

export default store