import request from '@/utils/request'
// 获取轮播图
export function apiGetBannerList() {
    return request({
        url: '/banner',
        method: 'get',
    })
}
// 获取推荐歌单
export function apiGetPersonList() {
    return request({
        url: '/personalized',
        method: 'get',
        params: {
            limit: 5
        }
    })
}
// 最新音乐
export function apiGetNewSong() {
    return request({
        url: '/personalized/newsong',
        method: 'get',
        params: {
            limit: 10
        }
    })
}

// 获取推荐MV
export function apiGetPersonMv() {
    return request({
        url: '/personalized/mv',
        method: 'get',
        params: {
            limit: 10
        }
    })
}
