<template>
  <div class="result-container">
    <div class="title-wrap">
      <h2 class="title">{{ $route.query.keywords }}</h2>
      <span class="sub-title">找到{{ songCount }}个结果</span>
    </div>
    <el-tabs v-model="activeIndex" @tab-click="handleClick">
      <el-tab-pane label="歌曲" name="songs">
        <table class="el-table">
          <thead>
            <th></th>
            <th>音乐标题</th>
            <th>歌手</th>
            <th>专辑</th>
            <th>时长</th>
          </thead>
          <tbody>
            <tr class="el-table__row" v-for="(item, index) in songs" :key="index" @dblclick="playMusic(item.id)">
              <td>{{ index + 1 }}</td>
              <td>
                <div class="song-wrap">
                  <div class="name-wrap">
                    <span>{{ item.name }}</span>
                    <!-- mv的图标 -->
                    <span class="iconfont icon-mv" v-if="item.mvid" @click="toMv(item.mvid)"></span>
                  </div>
                  <!-- 二级标题 -->
                  <span v-if="item.alias.length != 0">{{ item.alias[0] }}</span>
                </div>
              </td>
              <td>{{ item.artists[0].name }}</td>
              <td>{{ item.album.name }}</td>
              <td>{{ item.duration | formatDuration }}</td>
            </tr>
          </tbody>
        </table>
      </el-tab-pane>
      <el-tab-pane label="歌单" name="lists">
        <div class="items">
          <div class="item" v-for="(item, index) in playlists" :key="index" @click="toPlayList(item.id)">
            <div class="img-wrap">
              <div class="num-wrap">
                播放量:
                <span class="num">{{ item.trackCount | formatPlayCount }}</span>
              </div>
              <img :src="item.coverImgUrl" />
              <span class="iconfont icon-play"></span>
            </div>
            <p class="name">{{ item.name }}</p>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="MV" name="mv">
        <div class="items mv">
          <div class="item" v-for="(item, index) in mvs" :key="index">
            <div class="img-wrap">
              <img :src="item.cover" alt="" />
              <span class="iconfont icon-play"></span>
              <div class="num-wrap">
                <div class="iconfont icon-play"></div>
                <div class="num">{{ item.playCount | formatPlayCount }}</div>
              </div>
              <span class="time">{{ item.duration | formatDuration }}</span>
            </div>
            <div class="info-wrap">
              <div class="name">{{ item.name }}</div>
              <div class="singer">{{ item.artists[0].name }}</div>
            </div>
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import { apiSearch } from "@/api/top/search";
import { apiGetSongUrl } from "../api";
export default {
  name: "result",
  data() {
    return {
      activeIndex: "songs",
      keywords: this.$route.query.keywords,
      songs: [],
      songCount: 0,
      limit: 10,
      type: 0,

      // 歌单
      playlists: [],
      //mv
      mvs: [],
    };
  },

  created() {
    this.getSearchResult();
    this.type = 1;
  },
  filters: {
    //将播放量转化为万或者亿
    formatPlayCount(count) {
      if (count >= 100000000) {
        return (count / 100000000).toFixed(1) + "亿";
      } else if (count >= 10000) {
        return (count / 10000).toFixed(1) + "万";
      } else {
        return count;
      }
    },
  },
  watch: {
    type() {
      this.getSearchResult();
    },
  },
  methods: {
    getSearchResult() {
      // apiSearch(keywords, limit, type, offset)
      if (this.type === 1) {
        // 获取歌曲
        apiSearch(this.keywords, this.limit, 1).then((res) => {
          let { songs, songCount } = res.data.result;
          this.songs = songs;
          this.searchCount = songCount;
        });
      } else if (this.type === 1000) {
        // 获取歌单
        apiSearch(this.keywords, this.limit, 1000).then((res) => {
          console.log(res);
          let { playlists } = res.data.result;
          console.log(playlists);
          this.playlists = playlists;
        });
      } else {
        // 获取MV
        apiSearch(this.keywords, this.limit, 1004).then((res) => {
          let { mvs, playlistCount } = res.data.result;
          console.log(mvs);
          this.mvs = mvs;
        });
      }
    },
    toMv(mvid) {
      console.log(mvid);
      this.$router.push({
        path: "/mv",
        query: {
          mvid,
        },
      });
    },
    handleClick() {
      if (this.activeIndex === "lists") this.type = 1000;
      else if (this.activeIndex === "mv") this.type = 1004;
      else this.type = 1;
      this.getSearchResult();
    },

    async playMusic(id) {
      // 发送请求获取歌曲的url
      let url = await apiGetSongUrl(id);
      this.$store.dispatch("setMusicUrl", url);
    },
    // 跳转到歌单详情页面
    toPlayList(id) {
      console.log(id);
      this.$router.push({
        path: "/playlist",
        query: {
          id,
        },
      });
    },
  },
};
</script>

